function sliderMain() {
    var swiper = new Swiper('.main .swiper', {
        slidesPerView: '1',
        spaceBetween: 40,
        centeredSlides: true,
        loop: true,
        navigation: {
            nextEl: '.main .swiper-button-next',
            prevEl: '.main .swiper-button-prev',
        }
    })
}

let SlidersArray = []
function sliderClothes() {
    $(".clothes").each(function(index, el) {
        $(el).attr("data-slider-id", `${index}`)
        SlidersArray[index] = new Swiper(`.clothes[data-slider-id="${index}"] .swiper`, {
            slidesPerView: '4',
            spaceBetween: 20,
            loop: true,
            navigation: {
                nextEl: `.clothes[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `.clothes[data-slider-id="${index}"] .swiper-button-prev`,
            },
            breakpoints: {
                320: {
                    slidesPerView: 1.3,
                },
                577: {
                    slidesPerView: 2,
                },
                769: {
                    slidesPerView: 3,
                    spaceBetween: 15,
                },
                993: {
                    slidesPerView: 4,
                },
            }
        })
    })
}

function gallery() {
    var swiper = new Swiper(".gallery .mySwiper", {
        slidesPerView: 5,
        spaceBetween: 15,
        freeMode: true,
        watchSlidesProgress: true,
        direction: "horizontal",
    });
    var swiper2 = new Swiper(".gallery .mySwiper2", {
        spaceBetween: 10,
        slidesPerView: 1,
        thumbs: {
            swiper: swiper,
        },
        navigation: {
            nextEl: ".swiper-button-next",
            prevEl: ".swiper-button-prev",
        },
    });
}


$(document).ready(function() {

    sliderMain();
    sliderClothes();
    gallery();



    // Burger - Menu
    $('.header__burger').click(function () {
        $(this).toggleClass('header__burger--active')
        $('.header-menu').toggleClass('header-menu--active')
        $('body').toggleClass('fixed-body')
    })

    // Favourites
    $('.favourites').click(function () {
        $(this).toggleClass('favourites--active')
    })

    // Select
    $(".custom_select select").niceSelect()

    // Table - check
    $('.table-checkbox').click(function () {
        $(this).parents('.bg-tr').toggleClass('bg-tr--active')
    })

})